<template>
  <div class="app-container">
    <div class="filter-container">
      <el-button @click="showDialog(0)">新增bug等级</el-button>
      <el-button @click="dialogFormVisible = true">新增bug等级</el-button>
      <el-button @click="dialogFormVisible = true">新增bug等级</el-button>
    </div>
    <el-table :data="list" v-loading.body="listLoading" element-loading-text="拼命加载中" border fit highlight-current-row>
      <el-table-column align="center" label='ID' width="95">
        <template scope="scope">
          {{scope.$index}}
        </template>
      </el-table-column>
      <el-table-column label="bugId">
        <template scope="scope">
          {{scope.row.bugLevelId}}
        </template>
      </el-table-column>

      <el-table-column label="BUG名称" align="center">
        <template scope="scope">
          <span>{{scope.row.bugLevelName}}</span>
        </template>
      </el-table-column>
      <el-table-column label="创建时间" align="center">
        <template scope="scope">
          {{scope.row.createTime}}
        </template>
      </el-table-column>
      <el-table-column align="center" label="更新时间">
        <template scope="scope">
          <i class="el-icon-time"></i>
          <span>{{scope.row.updateTime}}</span>
        </template>
      </el-table-column>
      <el-table-column label="操作" width="150px">
        <template scope="scope">
          <el-button size="small" @click="showDialog(1, scope.row)">编辑</el-button>
          <el-button size="small" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="block">

      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage6"
        :page-size="pages.pageSize"
        :page-sizes="[10, 20, 30, 40]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="pages.total">
      </el-pagination>
    </div>

    <!-- Form -->

    <el-dialog :title="isEdit==0?'新增bug等级':'编辑bug等级'" :visible.sync="dialogFormVisible" :beforeClose="test">
      <el-form :model="form">
        <el-form-item label="等级名称" :label-width="formLabelWidth">
          <el-input v-model="form.bugLevelName" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="handleData">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import {getBugLevelList, insertBugLevel, updateBugLevel, deleteBugLevelByPrimaryKey} from '@/api/basic'

  export default {
    data() {
      return {
        list: null,
        listLoading: true,
        form: {
          bugLevelName: ''
        },
        pages: {
          total: '',
          pageNum: 1,
          pageSize: 10
        },
        dialogFormVisible: false,
        isEdit: 0
      }
    },
    created() {
      this.fetchData()
    },
    methods: {
      test() {
        this.fetchData()
      },
      fetchData() {
        this.listLoading = true
        getBugLevelList(this.pages).then(response => {
          console.log(response);
          this.pages.total = response.data.totalPage
          this.list = response.data.result
          this.listLoading = 0
        })
      },
      insertBugLevel() {
        return new Promise((resolve, reject) => {
          insertBugLevel(this.form).then(response => {
            this.dialogFormVisible = false
            resolve()
          }).catch(error => {
            reject(error)
          })
        })
      },
      updateBugLevel() {
        return new Promise((resolve, reject) => {
          updateBugLevel(this.form).then(response => {
            this.dialogFormVisible = false
            this.fetchData()
            resolve()
          }).catch(error => {
            reject(error.msg)
          })
        })
      },
      deleteBugLevelByPrimaryKey() {
        return new Promise((resolve, reject) => {
          deleteBugLevelByPrimaryKey(this.form).then(response => {
            this.dialogFormVisible = false
            this.fetchData()
            resolve()
          }).catch(error => {
            reject(error.msg)
          })
        })
      },
      showDialog(index, row) {
        if (row != undefined) this.form = row;
        this.isEdit = index;
        this.dialogFormVisible = true
      },
      handleData() {
        switch (this.isEdit) {
          case 0:
            this.insertBugLevel();
            break;
          case 1:
            this.updateBugLevel();
            break;
        }
      },
      handleDelete() {
        this.$confirm('确认删除？')
          .then(_ => {

            done();
          })
          .catch(_ => {
          });
      },
      handleSizeChange(size) {
        this.pages.pageSize = size
        this.fetchData();
      },
      handleCurrentChange(currentPage) {
        this.pages.pageNum = currentPage
        this.fetchData();
      }
    },
    watch: {
      isEdit: function (val, oldVa) {
        if (val == 0) {
          for (var p in this.form) {
            if (typeof ( this.form [p]) != " function ") {
              this.form [p] = '';
            }
          }
        }
      }
    }
  }
</script>



